import React from "react";
import ReactEcharts from "echarts-for-react"

class IdmChart extends React.Component {
    getOptions = (props) => {
        let chart_tahun = [];
        props.resultData.data.idm.forEach((item, index) => {
            chart_tahun[index] = item.tahun
        })
        let chart_idm = [];
        props.resultData.data.idm.forEach((item, index) => {
            chart_idm[index] = item.skor_idm
        })
        return {
            tooltip: {
                trigger: 'axis',
                axisPointer: {
                    type: 'none'
                }
            },
            legend: {},
            grid: {
                height: '80%',
                left: '0',
                right: '4%',
                top: '5%',
                bottom: '20%',
                containLabel: true
            },
            xAxis: [
                {
                    type: 'category',
                    data: chart_tahun,
                    axisTick: {
                        show: false
                    },
                    axisLine: {
                        lineStyle: {
                            width: 0,
                            type: "dashed"
                        }
                    },
                    splitLine: {
                        show: true,
                        lineStyle: {
                            type: "dashed"
                        }
                    }
                }
            ],
            yAxis: [
                {
                    type: 'value',
                    min: 0,
                    max: 1,
                    splitNumber: 10,
                    data: chart_idm,
                    splitLine: {
                        show: true,
                        lineStyle: {
                            type: "dashed"
                        }
                    }
                }
            ],
            series: [
                {
                    type: 'line',
                    data: chart_idm,
                    symbolSize: 10,
                    color: '#E58B20'
                },
            ]
        }
    }

    render() {
        return (
            <ReactEcharts
                option={this.getOptions(this.props)}
                style={{ width: "auto", height: "100%" }}
            ></ReactEcharts>
        )
    }
}

export default IdmChart
